import React, { useState } from "react";
import Navbar from "../Navbar/Navbar";
import "./Services.css";

export default function Services() {
    const [ServiceActive, setServiceActive] = useState(false)
  return (
    <>
    <div className="bg-blue">
    <Navbar/>
    </div>
        <div className="head">
            <div className="head-layout mt-1">
                <h1>S</h1>
                <h2>Services</h2>
                <div className="line"></div>
            </div>
        </div>
      <div className="ServicesConPage my-5 container">
        <div className="row gx-5">
          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5 me-6"  :"serviceCon row shadow-lg col-md-5 me-6"}>
            <div className="iconCon col-2">
              <i class="fa-solid fa-dna"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Appraisal</h3>
            </div>
            <div className="ArrowCon col-2" onClick={() => setServiceActive(!ServiceActive)}>
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>

          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5"  :"serviceCon row shadow-lg col-md-5"}>
            <div className="iconCon col-2">
            <i class="fa-solid fa-sign-hanging"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Valuation</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>

          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5 me-6"  :"serviceCon row shadow-lg col-md-5 me-6"}>
            <div className="iconCon col-2">
            <i class="fa-solid fa-id-card"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Business Profile</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>

          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5"  :"serviceCon row shadow-lg col-md-5"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-cart-shopping"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Buying</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>

          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5 me-6"  :"serviceCon row shadow-lg col-md-5 me-6"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-rotate"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Exchange</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>


          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5"  :"serviceCon row shadow-lg col-md-5"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-house-flag"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Renting </h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>


          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5 me-6"  :"serviceCon row shadow-lg col-md-5 me-6"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-hand-holding-dollar"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Selling</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>


          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5"  :"serviceCon row shadow-lg col-md-5"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-city"></i>
            </div>
            <div className="headConService col-8">
              <h3>Residential Property</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>


          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5 me-6"  :"serviceCon row shadow-lg col-md-5 me-6"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-dumpster"></i>
            </div>
            <div className="headConService col-8">
              <h3>Commercial Property </h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>

          <div className={ServiceActive ? "serviceConActive row shadow-lg col-md-5"  :"serviceCon row shadow-lg col-md-5"}>
            <div className="iconCon col-2 ">
            <i class="fa-solid fa-gavel"></i>
            </div>
            <div className="headConService col-8">
              <h3>Real Estate Consulting</h3>
            </div>
            <div className="ArrowCon col-2">
              <i class="fa-solid fa-angle-down"></i>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
