import React from 'react'
import headerCon from '../../assets/images/headerCon.jpeg'
import Navbar from '../Navbar/Navbar';



export default function Header() {

  return <>
     <header>
    <div className="headerCon p-0">
        <img src={headerCon} alt="" className='headerImage'/>
        <i className="fa-solid fa-location-dot locationIcon"></i>
    <h1 className="fw-bolder head-font">GET A PROPERTY NOW <br/>
    AFFORDABLE PRICE .</h1>
    </div>
    <Navbar/>
        </header>
  </>
}
