import { collection, onSnapshot, query, updateDoc , doc, addDoc, deleteDoc } from "firebase/firestore";
import { getDownloadURL, listAll, ref, uploadBytes } from "firebase/storage";
import Joi from "joi";
import React, { useEffect, useState } from "react";
import { db, storage } from "../../firebase";
import LoadingPage from "../LoadingPage/LoadingPage";
import Header from "../Header/Header";




export default function CRUD() {
  const [Properties, setProperties] = useState([]);
  const [property, setProperty] = useState({
    nameOfProperty: "",
    PriceOfProperty: "",
    address: "",
    desc: "",
    beds: "",
    baths: "",
    Upload_Photo: 'images/',
    id:''
  });
    
  const [pageLoading, setPageLoading] = useState(false);
  const [DeletedProperty, setDeletedProperty] = useState(null);
  const [success, setSuccess] = useState(null);
  const [successUpdated, setSuccessUpdated] = useState(null);
  const [isLoading, setisLoading] = useState(false);
  const [errorList, setErrorList] = useState([]);
  const [errorListUpdated, seterrorListUpdated] = useState([]);
  const [UpdatedPropertyId, setUpdatedPropertyId] = useState(null);
  const [ImageUploaded, setImageUploaded] = useState(null);
  const [ImagesList, setImagesListCrud] = useState([]);
  
  // .................. Create Property .........................
  function uploadImage(property){
    if(ImageUploaded == null) return;
    const ImageRef = ref(storage , `images/${ImageUploaded.name + property.nameOfProperty}`)
    uploadBytes(ImageRef , ImageUploaded).then((snapShot) => {
      getDownloadURL(snapShot.ref).then((url) => {
        setImagesListCrud((prev) => [...prev , url])
      })
    })
  }

  const ImageListRef = ref(storage , 'images/')

  useEffect(() => {
    listAll(ImageListRef).then((res) => {
      res.items.forEach((item) => {
        getDownloadURL(item).then((url) => {
          setImagesListCrud((prev) => [...prev , url])
          console.log(ImagesList);
        })
      })
    })
  }, [])

  function getProperty(e) {
    let userClone = { ...property };
    userClone[e.target.name] = e.target.value;
    setProperty(userClone);
    console.log(property);
  }

  async function SubmitLoginForm(e) {
    e.preventDefault();
    setSuccess(null);
    setErrorList([]);
    setPageLoading(true)
    let ValidationCheck = ValidationUser(property);
    if (ValidationCheck.error) {
      setErrorList([ValidationCheck.error.details[0].message]);
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    setPageLoading(false)
    } else {
      if (ImageUploaded) {
        uploadImage(property);
        await addDoc(collection(db , 'properties') , property
        )
        setSuccess('Added Property Successfully');
        setErrorList([])
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        });
        setPageLoading(false)
        setProperty({
          nameOfProperty: "",
          PriceOfProperty: "",
          address: "",
          desc: "",
          beds: "",
          baths: "",
          Upload_Photo:'images/',
          id:''
        })
        setImageUploaded(null)
      }else {
        setErrorList(['You must Upload Image'])
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        });
      setPageLoading(false)
      }

    }
  }

  
  // .................. Get Property ....................
  useEffect(() => {
    setPageLoading(true)
    const q = query(collection(db , 'properties'));
    const unSubscribe = onSnapshot(q , (querySnapshot) => {
      let propertiesArr = [];
      querySnapshot.forEach((doc) => {
        propertiesArr.push({...doc.data() , id : doc.id})
      })
      setProperties(propertiesArr)
      setPageLoading(false)
    })
    return () => unSubscribe()
  }, [])


 // ................ Update Property ..................
  const [updated, setUpdated] = useState({
    nameOfProperty: "",
    PriceOfProperty: "",
    address: "",
    desc: "",
    beds: "",
    baths: "",
    Upload_Photo: "",
    id: '',
  });
  function GetUpdatedPropertyId(e) {
    console.log(e.target.id);
    setUpdatedPropertyId(e.target.id)
  }
  function getUpdated(e) {
    let userClone = { ...updated };
      userClone['id'] = UpdatedPropertyId;
      userClone[e.target.name] = e.target.value;
    setUpdated(userClone);
    console.log(updated);
  }
  
  const UpdateProperty = async (event , updated) => {
    setisLoading(true)
    event.preventDefault();
    let ValidationCheck = ValidationUser(updated);
    if (ValidationCheck.error) {
      seterrorListUpdated([ValidationCheck.error.details[0].message]);
      document.getElementById('exampleModal').scrollTo({
        top: 0,
        behavior: "smooth"
      });
      setisLoading(false)
    } else {
      await updateDoc(doc(db, 'properties' , updated.id) , {
        nameOfProperty: updated.nameOfProperty ,
        PriceOfProperty: updated.PriceOfProperty,
        address: updated.address,
        desc: updated.desc,
        beds: updated.beds,
        baths: updated.baths,
        Upload_Photo: updated.Upload_Photo
      })
      setisLoading(false)
      seterrorListUpdated([])
      document.getElementById('exampleModal').scrollTo({
        top: 0,
        behavior: "smooth"
      });
      
      setSuccessUpdated('Property Updated Successfully')
      console.log('updated');
    }

  }


// .............. Delete Property ................
  function GetDeletePropertyId(e) {
    console.log(e.target.id);
    setDeletedProperty(e.target.id);  
  }
 async function DeleteProperty(){
  // console.log(property.nameOfProperty);
  // for (let i = 0; i < ImagesList.length; i++) {
  //   if(ImagesList[i].includes(property.nameOfProperty)){
  //     console.log(ImagesList[i]);
  //     console.log('here');
  //   }
  // }
  console.log(DeletedProperty);
   await deleteDoc(doc(db , 'properties' , DeletedProperty));
   console.log('Deleted');
  }
// ............... Validation Property ......................
  function ValidationUser(property) {
    let schema = Joi.object({
      nameOfProperty: Joi.string().max(40).min(3).required(),
      PriceOfProperty: Joi.string().max(12).min(3).required(),
      address: Joi.string().max(300).min(5).required(),
      beds: Joi.string().max(3).min(1).required(),
      baths: Joi.string().max(3).min(1).required(),
      desc: Joi.string().max(300).min(5).required(),
      Upload_Photo: Joi.string().required(),
      id: Joi.any().required(),
    });
    return schema.validate(property, { abortEarly: false });
  }
  return ( <>
  {pageLoading ? <LoadingPage/> : ''}
    <Header/>
      <div className="ConStudents">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="shadow-lg p-3 bg-tranparent rounded w-100 row m-auto mb-5 mt-3">
                <form onSubmit={SubmitLoginForm} className="col-md-12 formProperties ">
                  {errorList.map((err, indx) => (
                    <div
                      key={indx}
                      className="alert alert-danger mt-4"
                      role="alert"
                    >
                      <i className="fa-solid fa-triangle-exclamation me-2"></i>{" "}
                      {err}{" "}
                    </div>
                  ))}
                  {success ? (
                    <div className="alert alert-success mt-4" role="alert">
                      <i className="fa-solid fa-check-double me-2"></i>{" "}
                      {success}
                    </div>
                  ) : (
                    ""
                  )}
                  <div className="d-flex w-100">
                    <label
                      className="w-50 me-3 my-2"
                      htmlFor="exampleInputEmail1"
                      id="name"
                    >
                      Name Of Property
                    </label>
                    <label className="w-50 my-2" htmlFor="exampleInputEmail1">
                      Price Of Property
                    </label>
                  </div>
                  <div className="d-flex">
                    <input
                      type="text"
                      className="input me-3 w-50"
                      id="exampleInputEmail1"
                      name="nameOfProperty"
                      onChange={getProperty}
                    />
                    <input
                      type="number"
                      className="input w-50"
                      id="exampleInputEmail1"
                      name="PriceOfProperty"
                      onChange={getProperty}
                    />
                  </div>
                  <div className="mb-3">
                    <label
                      htmlFor="exampleInputEmail1"
                      className="w-50 me-3 my-2"
                    >
                      Address
                    </label>
                    <input
                      type="text"
                      className="input w-100"
                      name="address"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      onChange={getProperty}
                    />
                  </div>
                  <div className="mb-3">
                    <label
                      htmlFor="exampleInputEmail1"
                      className="w-50 me-3 my-2"
                    >
                      Description
                    </label>
                    <input
                      type="text"
                      className="input w-100"
                      name="desc"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      onChange={getProperty}
                    />
                  </div>
                  <div className="d-flex w-100">
                    <label
                      className="w-50 me-3 my-2"
                      htmlFor="exampleInputEmail1"
                    >
                      Beds
                    </label>
                    <label className="w-50 my-2" htmlFor="exampleInputEmail1">
                      Baths
                    </label>
                  </div>
                  <div className="d-flex">
                    <input
                      type="number"
                      className="input me-3 w-50"
                      id="exampleInputEmail1"
                      name="beds"
                      onChange={getProperty}
                    />
                    <input
                      type="number"
                      className="input w-50"
                      id="exampleInputEmail1"
                      name="baths"
                      onChange={getProperty}
                    />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="formFile" className="form-label">
                      Default file input example
                    </label>
                    <input
                      onChange={(event) => setImageUploaded(event.target.files[0])}
                      className="form-control"
                      type="file"
                      id="formFile"
                      name="Upload_Photo"
                      accept="image/png, image/jpg, image/gif, image/jpeg"
                    />
                  </div>                
                  <button type="submit" className="button w-100">
                    Add Property
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid tableConT  rounded-3 shadow-lg mb-5 p-0">
        <table className="table tableCon rounded-3 table-striped mt-6 p-3">
          <thead>
            <tr>
              <th scope="col">Name of Property</th>
              <th scope="col">Price of Property</th>
              <th scope="col">Address</th>
              <th scope="col">Beds</th>
              <th scope="col">Baths</th>
              <th scope="col">Update</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>
            {Properties
              ? Properties.map((property, index) => (
                  <tr key={index}>
                    <td className="tdata">{property.nameOfProperty}</td>
                    <td className="tdata">{property.PriceOfProperty}</td>
                    <td className="tdata">{property.address}</td>
                    <td className="tdata">{property.beds}</td>
                    <td className="tdata">{property.baths}</td>
                    <td className="tbutton">
                      <button
                        type="button"
                        className="btn btn-warning"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal"
                        id={property.id}
                        data-bs-whatever="@mdo"
                        onClick={GetUpdatedPropertyId}
                      >
                        Update
                      </button>
                      <div
                        className="modal fade"
                        id="exampleModal"
                        tabIndex="-1"
                        aria-labelledby="exampleModalLabel"
                        aria-hidden="true"
                      >
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h5
                                className="modal-title"
                                id="exampleModalLabel"
                              >
                                Update User
                              </h5>
                              <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                              ></button>
                            </div>
                            <div className="modal-body">
                            <form onSubmit={(e) => UpdateProperty(e , updated)} className="col-md-12 form">
                  {errorListUpdated.map((err, indx) => (
                    <div
                      key={indx}
                      className="alert alert-danger mt-4"
                      role="alert"
                    >
                      <i className="fa-solid fa-triangle-exclamation me-2"></i>{" "}
                      {err}{" "}
                    </div>
                  ))}
                  {successUpdated ? (
                    <div className="alert alert-success mt-4" role="alert">
                      <i className="fa-solid fa-check-double me-2"></i>{" "}
                      {successUpdated}
                    </div>
                  ) : (
                    ""
                  )}

                  <div className="d-flex w-100">
                    <label
                      className="w-50 me-3 my-2"
                      htmlFor="exampleInputEmail1"
                      id="name"
                    >
                      Name Of Property
                    </label>
                    <label className="w-50 my-2" htmlFor="exampleInputEmail1">
                      Price Of Property
                    </label>
                  </div>
                  <div className="d-flex">
                    <input
                      type="text"
                      className="input me-3 w-50"
                      id="exampleInputEmail1"
                      name="nameOfProperty"
                      onChange={getUpdated}
                    />
                    <input
                      type="number"
                      className="input w-50"
                      id="exampleInputEmail1"
                      name="PriceOfProperty"
                      onChange={getUpdated}
                    />
                  </div>
                  <div className="mb-3">
                    <label
                      htmlFor="exampleInputEmail1"
                      className="w-50 me-3 my-2"
                    >
                      Address
                    </label>
                    <input
                      type="text"
                      className="input w-100"
                      name="address"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      onChange={getUpdated}
                    />
                  </div>
                  <div className="mb-3">
                    <label
                      htmlFor="exampleInputEmail1"
                      className="w-50 me-3 my-2"
                    >
                      Description
                    </label>
                    <input
                      type="text"
                      className="input w-100"
                      name="desc"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      onChange={getUpdated}
                    />
                  </div>
                  <div className="d-flex w-100">
                    <label
                      className="w-50 me-3 my-2"
                      htmlFor="exampleInputEmail1"
                    >
                      Beds
                    </label>
                    <label className="w-50 my-2" htmlFor="exampleInputEmail1">
                      Baths
                    </label>
                  </div>
                  <div className="d-flex">
                    <input
                      type="number"
                      className="input me-3 w-50"
                      id="exampleInputEmail1"
                      name="beds"
                      onChange={getUpdated}
                    />
                    <input
                      type="number"
                      className="input w-50"
                      id="exampleInputEmail1"
                      name="baths"
                      onChange={getUpdated}
                    />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="formFile" className="form-label">
                      Default file input example
                    </label>
                    <input
                      onChange={getUpdated}
                      className="form-control"
                      type="file"
                      id="formFile"
                      name="Upload_Photo"
                      accept="image/png, image/jpg, image/gif, image/jpeg"
                    />
                  </div>
                  <button type="submit" className="button w-100">
                   {isLoading ? 
    <i className="fa-solid fa-spinner fa-spin fs-1 color-green"></i>
   : "Save Changes"} 
                  </button>
                </form>
                            </div>
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="tbutton" id={property.id}>
                      <button
                        onClick={GetDeletePropertyId}
                        id={property.id}
                        type="button"
                        className="btn btn-danger"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal1"
                      >
                        Delete
                      </button>

                      <div
                        className="modal fade"
                        id="exampleModal1"
                        tabIndex="-1"
                        aria-labelledby="exampleModalLabel1"
                        aria-hidden="true"
                      >
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h5
                                className="modal-title"
                                id="exampleModalLabel1"
                              >
                                Delete Confirmation
                              </h5>
                              <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                              ></button>
                            </div>
                            <div className="modal-body">
                              are you sure for delete this student ?
                            </div>
                            <div className="modal-footer">
                              <button
                                type="button"
                                className="btn btn-secondary"
                                data-bs-dismiss="modal"
                              >
                                Close
                              </button>
                              <button
                                data-bs-dismiss="modal"
                                type="button"
                                className="btn btn-danger"
                                onClick={DeleteProperty}
                              >
                                Save Changes
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </td>
                  </tr>
                ))
              : ""}
          </tbody>
        </table>
      </div>
    </>
  );
}
