import React, { useState } from 'react'
import Footer from '../Footer/Footer'
import GetInTouch from '../GetInTouch/GetInTouch'
import MiddleWords from '../MiddleWords/MiddleWords'
import Header from '../Header/Header'
import Properties from '../Properties/Properties'
import Sales from '../Sales/Sales'


export default function Home() {
  const [pageLoading, setPageLoading] = useState(false);

  return <>
            <Header/>
            <div className="head">
  <div className="head-layoutt text-center mb-5">
      <h1>H</h1>
      <h2>ARE YOU THINKING HOME?</h2>
      <div className="lineFocus"></div>
  </div>
</div>
<div className="container">
  <div className="about row">
      <div className="about-paraa col-md-12 text-center">
          <p>THINK BigGroup offers great customer service, accurate inventory.</p>
      </div>
  </div>
</div>
        <Properties/>
        <div className="head">
  <div className="head-layoutt text-center mb-5">
      <h1>T</h1>
      <h2>TIME TO GET A CURRENT PROPERTY APPRAISAL.</h2>
      <div className="lineFocus"></div>
  </div>
</div>
<div className="container">
  <div className="about row">
      <div className="about-paraaTime col-md-12 text-center">
          <p>Are you thinking of selling your home? <br/> If so, the first thing you probably want to know is how much it might be worth.<br/> This will help you plan your sales strategy, minimize the time your home is on the market, and plan your immediate financial future after the sale. 
Contact Think Big Group to work with you and for you to find out how much your property is worth ?</p>
      </div>
  </div>
</div>
        <Sales setPageLoading={setPageLoading}/>
        <MiddleWords/>
        <GetInTouch/>
        <Footer/>
  </>
}
