import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import icon from './logoOnly.png'

export default function Navbar() {

    
    const [navbar, setNavbar] = useState(false)

    const changeBackground = () => {
      if (window.scrollY >= 66) {
        setNavbar(true)
      } else {
        setNavbar(false)
      }
    }
   
    useEffect(() => {
      changeBackground()
      window.addEventListener("scroll", changeBackground)
    })
  return <>
              <nav className={navbar ? "navbar navbar-expand-lg navbar-dark activeNavbar" : "navbar navbar-expand-lg navbar-dark mt-3 bg-transparent"} 
                id="navigation-panel">
                <div className="container" id="navigation-panel">
                   <Link className='link' to={'/home'}><a className="navbar-brand fw-bolder" href="#"><img src={icon} alt="Logo"
                            width="50px" height="30px"/> Think Big</a></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse"
                        data-bs-target="#navbarSupportedContent"
                        aria-controls="navbarSupportedContent" aria-expanded="false"
                        aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                            <li className="nav-item mx-2">
                            <Link className='link' to={'/home'}><a className="nav-link active" >Home</a></Link>
                                <div className="line2"></div>
                            </li>
                            <li className="nav-item mx-2 ">
                            <Link to={'/about'} className='link'> <a className="nav-link" >About</a></Link>
                                <div className="line3"></div>
                            </li>
                            <li className="nav-item mx-2">
                            <Link to={'/contacts'} className='link'> <a className="nav-link" >Contacts</a></Link>
                                <div className="line3"></div>

                            </li>
                            <li className="nav-item mx-2">
                             <Link to={'/services'} className='link'> <a className="nav-link" >Services</a></Link>
                                <div className="line3"></div>

                            </li>
                            <li className="nav-item mx-2">
                                <Link className='link' to={'/dashboard'}><a className="nav-link " href="#">Dashboard</a></Link>
                            </li>
                            <li className="nav-item mx-2">
                                <a className="nav-link " href="#"><i className="fab fa-twitter"></i></a>
                            </li>
                            <li className="nav-item mx-2">
                                <a className="nav-link " href="#"><i className="fab fa-facebook-f"></i></a>
                            </li>
                            <li className="nav-item mx-2">
                                <a className="nav-link " href="#"><i className="fab fa-instagram"></i></a>
                            </li>

                        </ul>
                    </div>
                </div>
            </nav>
  </>
}
