import { collection, onSnapshot, query } from 'firebase/firestore'
import { getDownloadURL, listAll, ref } from 'firebase/storage';
import React, { useEffect, useState } from 'react'
import { db, storage } from '../../firebase'

export default function Sales({setPageLoading}) {

  const [ImagesList, setImagesList] = useState([]);
  const ImageListRef = ref(storage , 'images/')

  useEffect(() => {
    listAll(ImageListRef).then((res) => {
      res.items.forEach((item) => {
        getDownloadURL(item).then((url) => {
          setImagesList((prev) => [...prev , url])
          console.log(ImagesList);
        })
      })
    })
  }, [])
    
  const [Properties, setProperties] = useState([]);

    useEffect(() => {
        const q = query(collection(db , 'properties'));
        const unSubscribe = onSnapshot(q , (querySnapshot) => {
          let propertiesArr = [];
          querySnapshot.forEach((doc) => {
            propertiesArr.push({...doc.data() , id : doc.id})
          })
          setProperties(propertiesArr)
          setPageLoading(false)
        })
        return () => unSubscribe()
      }, [])

  return         <div className="body">

  {/* .................... Slider for Sales ...................... */}

  <div id="carouselExampleControls" className="carousel py-5 slide w-100 my-5 cursor" data-bs-ride="carousel">
<div className="carousel-inner w-100 my-5">
{Properties.map((property , indx) => <>
    <div className={`carousel-item ${indx == 0 ? 'active' : ''}`}>
    <div className="sliderItem shadow-lg  w-75 row">
    <div className="imgConSlider d-block col-md-8 m-0 p-0">
    <img src={ImagesList[indx]} className="d-block w-100" alt="..."/>
    </div>    
        <div className="ItemContentSlider col-md-4 m-0">
        <h1>{property.nameOfProperty}</h1>
        <h3 className="PriceOfSlider">${property.PriceOfProperty}</h3>
        <p className="address"><i className="fa-solid fa-location-pin"></i>{property.address}</p>
        <div className="desc">
            <p className="descLabel">Description :</p>
            <p className="descContent">{property.desc}</p>
        </div>
        <ul className='ulSlider'>
            <li><i className="fa-solid fa-bed me-2"></i><p>Beds : </p><span>{property.beds}</span></li>
            <li><i className="fa-solid fa-bath me-2"></i><p>Baths : </p><span>{property.baths}</span></li>
        </ul>
        <button className='SliderButt'>View Details</button>
    </div>
    </div>
  </div>
  </>
)}

</div>
<button className="carousel-control-prev SliderArrow" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
<span className="carousel-control-prev-icon" aria-hidden="true"></span>
<span className="visually-hidden">Previous</span>
</button>
<button className="carousel-control-next SliderArrow" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
<span className="carousel-control-next-icon" aria-hidden="true"></span>
<span className="visually-hidden">Next</span>
</button>
</div>
  </div>
  {}
}
