import React from 'react'
import apartment from '../../assets/images/icons/apartment.png'
import farmhouse from '../../assets/images/icons/farmhouse.png'
import individualVilla from '../../assets/images/icons/individualVilla.png'
import modernhome from '../../assets/images/icons/modernhome.png'
import officeSpace from '../../assets/images/icons/officeSpace.png'
import townspace from '../../assets/images/icons/townspace.png'

export default function Properties() {
  return <>
          <div className="container my-5 cursor">
            <div className="services-div row cursor">
            <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={apartment} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Apartment</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
                <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={farmhouse} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Farm House</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
                <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={individualVilla} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Individual House</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
                <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={modernhome} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Modern House</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
                <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={officeSpace} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Office Space</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
                <div className=" col-md-4 my-3 cursor">
                    <div className="shadow-lg p-4 item d-flex cursor">
                    <div className="icon-div"><img src={townspace} alt="" srcSet="" /></div>
                    <div className="content mt-2">
                        <h6>Town House</h6>
                        <p>(5 Properties)</p>
                    </div>
                    </div>
                </div>
            </div>
        </div>
  </>
}
