import React from 'react'

export default function GetInTouch() {
  return <>
   <div className="head cursor">
            <div className="head-layout">
                <h1>G</h1>
                <h2>Get In Touch</h2>
                <div className="line"></div>
            </div>
        </div>
        <div className="info-div" id="information">
            <div className="container">
                <div className="row my-5">
                    <div className="info col-lg-12 col-sm-12">
                        <h2>Get answers and advices from professional consultants.</h2>
                        <div className="info-con">
                            <div className="info-item d-flex">
                                <div className="iconn-div"><i className="far fa-map"></i></div>
                                <div className="content-info">
                                    <h4>Address</h4>
                                    <p>1650 Lombard Street, San Francisco, CA 94123</p>
                                </div>
                            </div>
                            <hr/>
                            <div className="info-item d-flex">
                                <div className="iconn-div"><i className="fas fa-mobile-alt"></i></div>
                                <div className="content-info">
                                    <h4 className=" mx-2">Phone</h4>
                                    <p className=" mx-2">+1 (415) 876-3250 / +1 (415) 876-3251</p>
                                </div>
                            </div>
                            <hr/>
                            <div className="info-item d-flex">
                                <div className="iconn-div"><i className="far fa-envelope"></i></div>
                                <div className="content-info">
                                    <h4>Say Hello</h4>
                                    <p>Email Protected</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  
  </>
}
