import React from 'react'
import { Link } from 'react-router-dom'
import img from './logowithThink.png'
import './LandingPage.css';
export default function LandingPage() {
  return <>
  <div className="container cursor">
    <div className="row">
<div className="imgCon col-md-6 pt-5">
  <img src={img} alt="" className='w-100 mt-5' />
</div>
<div className="buttsCon col-md-6 d-flex flex-column justify-content-center vh-100 ">
    <div className="buttCon">
    <Link className='link' to={'/services'}><button className='buttLanding cursor w-100 p-3 my-3'>
        Services
    </button>
    </Link>
    </div>
    <div className="buttCon">
        <Link className='link' to={'/estates'}><button className='buttLanding cursor w-100 p-3 my-3'>
        Real Estates
    </button>
    </Link>
    </div>
</div>
    </div>
  </div>
  
  </>
}
